.syllabusWrapper {
  /* background-color: salmon; */
  padding: 2rem;
}

.boilerplatewrapper {
  /* background-color: salmon; */
  border: 2px solid rgb(57, 171, 107);
  padding: 2rem;
  border-radius: 1.2rem;
}

.boilerplateContainer {
  background-color: rgba(246, 243, 243, 0.5);
  border-radius: 1.2rem;
  padding: 2rem;
}

.markdown {
  background-color: rgba(246, 243, 243, 0.8);
  padding: 2rem;
  border-radius: 1.2rem;
}

.unitBlockWrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding-top: 1.2rem;
  justify-content: center;
  align-items: center;
}

.unitBlockItem {
  border: 2px solid rgb(57, 171, 107);
  padding: 1.2rem;
  /* width: 20rem;
  height: 20rem; */
  min-height: 2rem;
  margin-right: 2rem;
  margin-bottom: 2rem;
  border-radius: 1.2rem;
  /* display: flex;
  flex-direction: column;
  align-items: center; */
}

.innerunitBlock {
  background-color: rgba(246, 243, 243, 0.5);
  padding: 1.2rem;
  padding-top: 0;
  border-radius: 0.8rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: rgb(25, 68, 43);
}

.unitBlockItem h3 {
  padding: 1rem;
  font-size: 0.8rem;
  background-color: rgba(246, 243, 243, 0.5);
  border: 2px solid rgb(57, 171, 107);
  /* color: rgb(247, 241, 244); */
  border-radius: 0.6rem;
}

.unitBlockItem h3:hover {
  background-color: rgb(57, 171, 107);
  color: rgb(255, 1, 136);
}

.dayContainer {
  background-color: rgba(246, 243, 243, 0.5);
  border-radius: 0.6rem;
  padding-left: 1.2rem;
  padding-right: 1.2rem;
  /* border-bottom: 1px solid black; */
  font-size: 0.6rem;
  margin-bottom: 0.4rem;
}

.dayContainer:hover {
  background-color: rgb(57, 171, 107);
  color: rgb(255, 1, 136);
}

.asyncDayContainer {
  border-radius: 0.6rem;
  padding-left: 1.2rem;
  padding-right: 1.2rem;
  font-size: 0.7rem;
  margin-bottom: 0.4rem;
  background-color: rgba(20, 20, 200, 0.5);
  color: white;
}

.asyncDayContainer:hover {
  background-color: rgb(57, 171, 107);
  color: rgb(255, 1, 136);
}

.unitHeaderWrapper {
  /* background-color: salmon; */
  display: flex;
  flex-direction: row;
  padding: 0.8rem;
  align-items: center;
  flex-wrap: wrap;
}

.unitHeaderItem {
  /* background-color: hotpink;
  background-color: rgba(246, 243, 243, 0.5); */
  background-color: rgba(246, 243, 243, 0.1);
  border: 2px solid rgb(57, 171, 107);
  padding: 1.2rem;
  border-radius: 1.2rem;
  margin: 0 auto;
  margin-right: 0.8rem;
  margin-bottom: 0.8rem;
  font-size: 0.6rem;
  /* width: 5rem; */
}
.unitHeaderItem:hover {
  background-color: rgba(246, 243, 243, 0.5);
  border: 2px solid rgb(198, 24, 181);
}

.unitDetailWrapper {
  background-color: rgb(237, 175, 196);
  padding: 2rem;
}

.dayHeaderWrapper {
  /* background-color: salmon; */
  display: flex;
  flex-direction: row;
  padding: 0.8rem;
  align-items: flex-start;
  flex-wrap: wrap;
}
.dayHeaderItem {
  /* background-color: hotpink;
  background-color: rgba(246, 243, 243, 0.2); */
  padding: 0.4rem;
  border-radius: 1.6rem;
  /* margin: 0 auto; */
  margin-right: 0.4rem;
  margin-bottom: 0.4rem;
  font-size: 0.5rem;
}

.dayHeaderItem:hover {
  background-color: rgb(240, 248, 17);
  /* border: 1px solid red; */
}

.dayDetailWrapper {
  background-color: rgb(237, 175, 196);
  padding: 2rem;
}

@media screen and (min-width: 300px) {
  .syllabusWrapper {
    padding: 0.2rem;
  }
  .boilerplatewrapper {
    /* background-color: salmon; */
    border: 2px solid rgb(57, 171, 107);
    padding: 0.2rem;
    border-radius: 1.2rem;
  }

  .boilerplateContainer {
    background-color: rgba(246, 243, 243, 0.5);
    border-radius: 1.2rem;
    padding: 1.2rem;
  }
  .unitBlockItem {
    border: 2px solid rgb(57, 171, 107);
    padding: 1.2rem;
    min-height: 2rem;
    margin-right: 2rem;
    margin-bottom: 2rem;
    border-radius: 1.2rem;
  }
}

@media screen and (min-width: 600px) {
  .syllabusWrapper {
    padding: 2rem;
  }
  .boilerplatewrapper {
    /* background-color: salmon; */
    border: 2px solid rgb(57, 171, 107);
    padding: 2rem;
    border-radius: 1.2rem;
  }

  .boilerplateContainer {
    background-color: rgba(246, 243, 243, 0.5);
    border-radius: 1.2rem;
    padding: 2rem;
  }

  .unitBlockItem {
    border: 2px solid rgb(57, 171, 107);
    padding: 1.2rem;
    min-height: 2rem;
    margin-right: 2rem;
    margin-bottom: 2rem;
    border-radius: 1.2rem;
  }
}
